.navbar {
    background: rgb(30, 30, 30);
    height: 5rem;
    width: 100vw;
    max-width: 80rem;
    margin: 0 auto;
    padding: 3rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo {
    object-fit: cover;
    height: 5rem;
    width: 5rem;
}

.menuItem {
    margin: 1rem;
    cursor: pointer;
}

.menuItem:hover {
    color: #BC9D59;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid #BC9D59;
}

.menuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}

.menuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
    cursor: pointer;
}

.active {
    color: #BC9D59;
    padding: 0.5rem;
    border-bottom: 2px solid #BC9D59;
}

.mobMenu {
    display: none;
    object-fit: cover;
    height: 3rem;
}

.navMenu {
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem
}

.mobItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

@media screen and (max-width:745px) {
    .mobMenu {
        display: flex;
    }
    .menu {
        display: none;
    }
    .menuBtn {

        display: none;
    }
}